import {constants} from "./index"
import axios from "axios"
import { fromJS } from "immutable"

export const searchFocus = () =>({
    type:constants.SEARCH_FOCUS
})

export const searchBlur = () => ({
    type:constants.SEARCH_BLUR
})

export const mouseEnter = () => ({
    type:constants.MOUSE_ENTER
})

export const mouseLeave = () => ({
    type:constants.MOUSE_LEAVE
})

export const changePage = (page) => ({
    type:constants.CHANGE_PAGE,
    page
})


//使用reduxthunk之后可以返回一个函数
export const getList = () => {
    return (dispatch) => {
        console.log('使用redux-thunk在actionCreators里面直接返回一个函数 下一行直接发送ajax请求');
        axios.get('/api/headerList.json').then(
            (res)=>{
                console.log('ajax 请求成功 getlist',res.data);
                // 构建一个action发送给reducer 这里直接dispatch action 
                // change_list就是一个action 由本身的change_list函数制造
                dispatch(change_list(res.data.data))

            }
        ).catch(
            ()=>{
                console.log('ajax 请求数据失败 getList');
            }
        )
    }
}

//注意这里仍然需要按照正常acitonCreators方式返回一个({这样的对象}) 
//报错内容： Line 39:10:  Expected an assignment or function call and instead saw an expression  no-unused-expressions
const change_list = (data) => ({
    type:constants.CHANGE_LIST,
    data:fromJS(data),//把数据转换为immutable类型
    totalPage:Math.ceil(data.length/10)//获取数据的长度作为总页数
})




